.btn-slash {
  overflow: hidden;
  position: relative;
}

.btn-slash span {
  z-index: 20;
}

.btn-slash:after {
  background: #fff;
  content: "";
  height: 155px;
  left: -75px;
  opacity: .2;
  position: absolute;
  top: -50px;
  transform: rotate(35deg);
  transition: all 600ms cubic-bezier(0.19, 1, 0.22, 1);
  width: 50px;
  z-index: -10;
}

.btn-slash:hover:after {
  left: 120%;
  transition: all 600ms cubic-bezier(0.19, 1, 0.22, 1);
}

